.page-header-menu {
    position: relative;
}
  
.page-header-menu-button {
    position: absolute;
    top: 0;
    right: 0;
    width: 36px;
    height: 32px;
    margin-top: 10px;
    margin-bottom: 25px;
    padding: 0;

    background-color: transparent;
    border: none;

    &::before {
        content: "";
        display: inline-block;
        width: 100%;
        height: 100%;
        background-image: url("../img/menu.svg");
        background-repeat: no-repeat;
        background-position: center;
    }
}