.header-contact {
    max-width: 174px;
    font-family: $Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: $brown;

    margin: 0 0 0 auto;
    grid-column: 5 / span 4;

    position: relative;

    .header-contact__icon {
        position: absolute;
        top: 4px;
        left: -27px;
        width: 21px;
        height: 21px;
    }

    a {
       text-decoration: none;
       color: $brown;

       &:hover,
       &:focus {
           opacity: 0.8;
       }

       &:active {
        opacity: 0.6;
       }
    }

    button {
        font-family: $Montserrat;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
        color: $brown;
        background-color: transparent;
        border: none;
        outline: none;
        text-decoration: underline;
        margin: 0 0 0 -6px;
        cursor: pointer;

        &:hover,
        &:focus {
            opacity: 0.8;
        }
 
        &:active {
         opacity: 0.6;
        }
    }
}