.collections {
    padding-top: 100px;
    margin-bottom: 113px;
}

.collections__description {
    max-width: 573px;
    font-family: $Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    color: #695A59;

    margin: 5px 0 0 100px;
}

.collections-list {
    margin: 46px 0 0 0;
    padding: 0;
    list-style: none;

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 15px;
}

.collections-list__collection--big {
    grid-row: span 2;
}