.popular {
  margin-bottom: 103px;
}

.popular-item-list {
  margin: 16px 0 0 0;
  padding: 0;
  list-style: none;

  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: 20px;
}

.popular-item-list__item {
  grid-column: span 4;
  display: grid;
  grid-template-rows:
    240px
    auto;
  background-color: #ffffff;
}

.popular-item-list__horizontal-item {
  grid-column: span 6;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: 132px 132px;
  grid-column-gap: 20px;
}

.popular-item-list__horizontal-item .popular-item-list__description-block {
  grid-column: 2 / -1;
  padding: 34px 20px 28px 15px;
}

.popular-item-list__description-block {
  padding: 35px 30px 40px 36px;

  h3 {
    font-family: $OldStandard;
    font-style: normal;
    font-weight: normal;
    font-size: 32px;
    line-height: 36px;
    color: #695A59;

    margin: 0;
    padding: 0 0 23px 0;
  }

  p {
    font-family: $Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 22px;
    line-height: 24px;
    color: #343235;

    margin: 0;
    padding: 0 0 19px 0;
  }

  small {
    font-family: $Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: #695A59;

    opacity: 0.6;
  }

  a {
    display: block;
    font-family: $Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 16px;
    text-transform: uppercase;
    text-decoration: none;

    color: #9A8584;
    padding: 30px 0 0 0;

    position: relative;

    &::before {
      position: absolute;
      content: '';
      background-image: url('../img/arrow.svg');
      background-repeat: no-repeat;
      background-size: 80px 8px;
      background-position: center;
      width: 80px;
      height: 8px;
      bottom: 3px;
      left: 85px;
    }

    &:hover,
    &:focus {
        opacity: 0.8;
    }

    &:active {
        opacity: 0.6;
    }
  }
}