.promo {
    background-image: url('../img/main-bg2.png'), url('../img/main-bg.png');
    background-size: cover, cover;
    background-repeat: no-repeat, no-repeat;
    min-height: 520px;
    margin: 0 0 195px 0;

    h1 {
        font-family: $OldStandard;
        font-style: normal;
        font-weight: normal;
        font-size: 46px;
        line-height: 64px;
        text-transform: uppercase;

        color: $ivory;
        margin: 0;
        grid-column-start: 2;
        grid-column-end: 7;
        grid-row-start: 1;
        grid-row-end: 2;

        // padding: 70px 0 0 0;
        margin: 0 0 0 0;
    }

    p {
        font-family: $Montserrat;
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 32px;
        color: $ivory;

        opacity: 0.6;
        margin: 0;

        grid-column-start: 2;
        grid-column-end: 7;
        grid-row-start: 2;
        grid-row-end: 3;
    }

    button {
        width: 230px;
        font-family: $Montserrat;
        font-style: normal;
        font-weight: bold;
        font-size: 12px;
        line-height: 20px;
        text-align: center;
        text-transform: uppercase;
        color: $bordo;

        background-color: transparent;
        border: none;
        border-top: 1px solid $bordo;
        border-bottom: 1px solid $bordo;

        grid-column-start: 2;
        grid-column-end: 5;
        grid-row-start: 3;
        grid-row-end: 3;
    }
}

.promo-image {
        grid-column-start: 7;
        grid-column-end: 12;
        grid-row-start: 1;
        grid-row-end: 2;
        margin: 10px 0 0 10px;
}