.team {
    background-color: #343235;
    margin-top: -18px;
    padding-bottom: 120px;
}

.team-content {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 20px;
}


.team__description {
    font-family: $Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    color: #FBF3F0;
    margin: 15px 0 0 0;

    grid-column: 2 / 8;
}

.team__description--small {
    font-family: $Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: #FBF3F0;

    margin: 15px 0 0 0;

    grid-column: 9 / 13;
    grid-row: 1 / 2;
}

.team-image {
    display: block;
    grid-column: 1 / -1;

    margin: 45px 0 0 0;
}