.projects {
    margin-bottom: 120px;
}

.projects-slider {
    margin: 28px 0 0 0;
    padding: 0;
    list-style: none;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
}

.projects-slider__slide {
    display: grid;
    grid-template-rows:
      auto
      100px;
    grid-column: 2 / 10;
    margin: -12px 0 0 3px;
}

.projects-slider__slide img {
    grid-area: 1 / 1 / 3 / 2;
}


.slide-info {
    grid-area: 2 / 1 / 3 / 2;
    z-index: 1;
    background-color: rgba(52, 50, 53, 0.9);

    display: flex;
    justify-content: space-between;
    align-items: center;


    p {
        font-family: $OldStandard;
        font-style: normal;
        font-weight: normal;
        font-size: 32px;
        line-height: 36px;
        color: #F5F2ED;
        margin: 0 0 0 35px;
    }

    a {
        display: inline-block;
        font-family: $Montserrat;
        font-style: normal;
        font-weight: bold;
        font-size: 12px;
        line-height: 16px;
        text-transform: uppercase;
        text-decoration: none;

        color: #F5F2ED;
        margin: 0 35px 0 0;

        position: relative;

        &::after {
            content: "";
            display: inline-block;
            width: 33px;
            height: 8px;
            margin: 4px 0 0 15px;
            background-image: url("../img/arrow-small.svg");
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
        }

        &:hover,
        &:focus {
            opacity: 0.8;
        }

        &:active {
            opacity: 0.6;
        }
    }
}

.slider-controls {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    position: relative;
}

.slider-controls__button {
        width: 72px;
        height: 72px;
        border: none;
        border-radius: 50%;
        background: #343235;
        cursor: pointer;
        position: absolute;
        top: 310px;
        background-image: url('../img/slider-arrow.svg');

    &--prev {
        grid-column: 1 / 2;
        background-size: 33px 16px;
        background-position: center;
        background-repeat: no-repeat;
    }

    &--next {
        grid-column: 12 / 13;
        margin: 0 0 0 25px;
        background-size: 33px 16px;
        background-position: center;
        background-repeat: no-repeat;
        transform: rotate(180deg);
    }

    &:hover,
    &:focus {
        background-color: $bordo;
    }

    &:active {
        opacity: 0.6;
    }
}