.wrapper {
    max-width: 1166px;
    padding: 0 10px;
    margin: 0 auto;

    &--header {
        display: grid;
        grid-template-columns: repeat(12, 1fr);
    }

      &--promo {
        display: grid;
        grid-template-columns: repeat(12, 1fr);
        grid-template-rows: 182px 74px 52px;
        // grid-column-gap: 20px;
        grid-row-gap: 30px;
        padding: 70px 0 0 0;
    }

    &--about-company {
        display: grid;
        grid-template-columns: repeat(12, 1fr);
    }

    &--popular {
        display: grid;
        grid-template-columns: repeat(12, 1fr);
        padding: 0;
    }

    &--projects {
        display: grid;
        grid-template-columns: repeat(12, 1fr);
    }

    &--mission{
        display: grid;
        grid-template-columns: repeat(12, 1fr);
    }
}