.about-company {
    margin: 0 0 107px 0;
    h2 {
        font-family: $OldStandard;
        font-style: normal;
        font-weight: normal;
        font-size: 42px;
        line-height: 54px;
        text-transform: uppercase;

        color: #695A59;
        margin: 0 0 25px 0;

        position: relative;
        z-index: 2;
        grid-column-start: 2;
        grid-column-end: 12;

        &::before {
            position: absolute;
            content: 'About us';
            font-family: $OldStandard;
            font-style: normal;
            font-weight: normal;
            font-size: 140px;
            line-height: 110px;
            text-transform: uppercase;
            color: #EAE1DC;
            top: -20px;
            left: -100px;
            z-index: -1;
        }
    }

    p {
        font-family: $Montserrat;
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 32px;

        color: #695A59;
        grid-column-start: 2;
        grid-column-end: 8;
        margin: 20px 0 0 0;
    }

    aside {
        grid-column-start: 9;
        grid-column-end: 12;
        grid-row-start: 2;
        grid-row-end: 6;

        margin: 30px 0 0 13px;

        .aside-parag {
            font-family: $Montserrat;
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 24px;
            color: #695A59;
            margin: 5px 0 0 0;
        }
    }
}

.test {
    background-image: url('../img/about-company-bg2.png');
    background-repeat: no-repeat;
    background-position: cover;
    min-height: 304px;

    grid-column-start: 1;
    grid-column-end: -1;
    grid-row-start: 9;
    grid-row-end: 12;

    margin: 40px 0 20px 0;

    p {
        font-family: $Montserrat;
        font-style: normal;
        font-weight: 500;
        font-size: 26px;
        line-height: 48px;
        color: #FFFFFF;

        margin: 0;
        padding: 72px 20px 0 100px;
    }
}

.test-2 {
    grid-row-start: 12;
    grid-row-end: 14;
}

.about-figure {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    margin: 45px 0 20px 0;
    grid-column-start: 2;
    grid-column-end: 13;
    grid-row-start: 15;
    grid-row-end: 17;

    figcaption {
        font-family: $Montserrat;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
        color: #695A59;

        grid-column-start: 3;
        grid-column-end: 11;

        padding: 310px 0 0 0;
        margin: 0 0 0 5px;

        a {
            color: #695A59;

            &:hover,
            &:focus {
                opacity: 0.8;
            }

            &:active {
                opacity: 0.6;
            }
        }
    }
}

.test-3 {
    grid-column-start: 2;
    grid-column-end: 6;
    grid-row-start: 18;
    grid-row-end: 20;
}