.mission {
    background-color: #343235;
    padding-top: 120px;
}

.mission-content {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows:
      auto
      auto
      auto
      auto;
    grid-gap: 20px;
  }

.mission-content .section-header {
        grid-column-start: 7;
        grid-column-end: -1;
        grid-row-start: 1;
        grid-row-end: 2;

        grid-template-columns: repeat(12, 1fr);
  }

  .mission-content .section-header h2 {
    grid-column-start: 3;
    grid-column-end: 11;
    grid-row-start: 1;
    grid-row-end: 2;
    margin: 5px 0 0 0;
  }

  .mission-content .section-header p {
    margin: 0 0 0 0;
  }
  

  .mission-content video {
        grid-column-start: 1;
        grid-column-end: 7;
        grid-row-start: 1;
        grid-row-end: 3;
  }

  .mission-text {
        grid-column-start: 8;
        grid-column-end: 13;
        grid-row-start: 2;
        grid-row-end: 2;
        margin-top: -205px;

    p {
        font-family: $Montserrat;
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 32px;
        color: #FBF3F0;
        margin: 0 0 12px 0;

    }
}

.mission-img-small {
    grid-column-start: 4;
    grid-column-end: 6;
    grid-row-start: 3;
    grid-row-end: 4;
    // margin-top: 100px;
}

.mission-img-big {
    grid-column-start: 7;
    grid-column-end: -1;
    grid-row-start: 2;
    grid-row-end: 4;

    margin-top: 105px;
}