.section-header {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-column-gap: 20px;
    font-family: $OldStandard;
}
  
.section-header h2 {
    grid-area: 1 / 2 / 2 / span 5;
    z-index: 1;
    margin: auto 0;
    font-size: 42px;
    line-height: 54px;
    text-transform: uppercase;
    color: #695A59;
}

.section-header p {
    grid-area: 1 / 1 / 2 / -1;
    margin: 15px 0 0 0;
    font-size: 140px;
    line-height: 140px;
    text-transform: uppercase;
    color: #eae1dc;
}

.section-header-light h2 {
  color: #eee9e6;
}

.section-header-light p {
  color: rgb(62, 58, 60);
}