.header-address {
    max-width: 252px;
    font-family: $Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: $brown;

    margin: 0 0 0 auto;

    grid-column: 9 / span 3;

    position:  relative;

    &::before {
        position: absolute;
        content: '';
        top: 2px;
        left: -30px;
        width: 18px;
        height: 22px;
        background-image: url('../img/icon-pin.svg');
    }
}